.footer{
    width: 100%;
    height: vw(305);
    background-color: #2A2A2A;
    padding: vw(40) 0;
}

.footer__container{
    display: flex;
    align-items: center;
}

.footer__info{
    display: flex;
    flex-direction: column;
    width: vw(371);
}

p.footer__text{
    font-size: vw(14.8);
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-size: vw(20);
    line-height: 120%;
    letter-spacing: vw(1);
    padding-top: vw(38);
}

.footer__logo{
    width: vw(126);
    height: vw(62);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.footer__right{

    .menu__nav{
        width: vw(756);
        margin-left: 0;
    
        a.nav__item{
            font-size: vw(27);
        }
    }

}

.footer__right__info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: vw(45);

    .footer__right__text{
        p.footer__text{
            padding-top: 0;
        }
    }

    a.footer__number{
        font-family: 'Montserrat', sans-serif;
        color: #57C54E;
        font-size: vw(25);
        font-family: 500;
    }
}

.footer__right__last{
    margin-top: vw(45);
    display: flex;
    justify-content: space-between;
    align-items: center;

    .map__text{
        display: flex;
        align-items: center;

        .map__images{
            width: vw(21.46);
            height: vw(21.46);

            img{
                width: 100%;
                height: 100%;
            }
        }

        p.footer__text{
            padding-top: 0;
            padding-left: vw(5);
        }
    }
}

.search{
    width: vw(255);
    height: vw(51);
    border-radius: vw(40);
    border: vw(1) solid #fff;
    display: flex;
    align-items: center;

    input{
        width: 100%;
        outline: none;
        border: none;
        height: vw(41);
        background-color: transparent;
        padding-left: vw(15);
        font-size: vw(17);
        color: #888888;
        font-family: 'Montserrat', sans-serif;
    }

    .search__ico{
        width: vw(30);
        height: vw(30);
        margin-right: vw(15);
        cursor: pointer;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

::-webkit-search-decoration,
::-webkit-search-cancel-button,
::-webkit-search-results-button,
::-webkit-search-results-decoration { 
    display: none; 
}

@media (min-width: 1600px){
    .footer{
        height: vhigh(305);
        padding: vhigh(40) 0;
    }
    
    .footer__info{
        width: vhigh(371);
    }
    
    p.footer__text{
        font-size: vhigh(14.8);
        font-size: vhigh(20);
        letter-spacing: vhigh(1);
        padding-top: vhigh(38);
    }
    
    .footer__logo{
        width: vhigh(126);
        height: vhigh(62);

    }
    
    .footer__right{
        margin-left: vhigh(120);
    
        .menu__nav{
            width: vhigh(756);
        
            a.nav__item{
                font-size: vhigh(27);
            }
        }
    
    }
    
    .footer__right__info{
        margin-top: vhigh(45);
    
        a.footer__number{
            font-size: vhigh(25);
        }
    }
    
    .footer__right__last{
        margin-top: vhigh(45);
    
        .map__text{
            .map__images{
                width: vhigh(21.46);
                height: vhigh(21.46);
            }
    
            p.footer__text{
                padding-left: vhigh(5);
            }
        }
    }
    
    .search{
        width: vhigh(255);
        height: vhigh(51);
        border-radius: vhigh(40);
        border: vhigh(1) solid #fff;
    
        input{
            height: vhigh(41);
            padding-left: vhigh(15);
            font-size: vhigh(17);
        }
    
        .search__ico{
            width: vhigh(30);
            height: vhigh(30);
            margin-right: vhigh(15);
        }
    }
}