.benefits{
    margin-top: vw(70);
    position: relative;

    &::before{
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        width: vw(941);
        height: vw(2);
        background-color: #038E00;
        z-index: 10;
    }

    &::after{
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: vw(498);
        height: vw(2);
        background-color: #038E00;
        z-index: 10;
    }
}

.benefits__images{
    position: absolute;
    right: 0;
    top: 0;
    width: vw(768);
    height: vw(500);
    z-index: 5;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.benefits__container{
    height: vw(500);
    display: flex;
    justify-content: center;
    flex-direction: column;

    p.title{
        font-size: vw(27);
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        text-transform: uppercase;
    }

    p.text{
        font-size: vw(17);
        font-family: 'Montserrat', sans-serif;
        width: vw(450);
        padding-top: vw(38);
    }
}

@media (min-width: 1600px){
    .benefits{
        margin-top: vhigh(70);
    
        &::before{
            width: vhigh(1041);
            height: vhigh(2);
        }
    
        &::after{
            width: vhigh(558);
            height: vhigh(2);
        }
    }
    
    .benefits__images{
        width: vw(768);
        height: vw(500);
    }
    
    .benefits__container{
        height: vw(500);
    
        p.title{
            font-size: vhigh(27);
        }
    
        p.text{
            font-size: vhigh(17);
            width: vhigh(450);
            padding-top: vhigh(38);
        }
    }
}