.quiz{
    width: 100%;
    /* position: relative;
    display: grid;  */
    display: flex;
    align-items: flex-start;
}

.quiz__nav{
    width: fit-content;
    height: fit-content;
    position: relative;
    z-index: 1;
    
    .quiz__nav__images{
        width: vw(498);
        height: vw(1005);
        position: relative;
        z-index: 5;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .quiz__pagination{
        z-index: 10;
        position: absolute;
        top: vw(70);
        left: vw(78);

        .quiz__pag__item{
            cursor: pointer;
            font-size: vw(20);
            color: white;
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            text-transform: uppercase;
            margin-top: vw(45);

            &:first-child{
                margin-top: 0;
            }

            &.active{
                color: #57C54E;
            }
        }
    }
}

.guiz__page{
 
    .quiz__page__content{
        height: vw(1005);
        position: relative;

        p.header{
            text-align: center;
            padding-top: vw(70);
            font-size: vw(27);
            font-family: 'Montserrat', sans-serif;
            font-weight: 550;
            text-transform: uppercase;
        }
    }
}

/* #first__page{
    .quiz__page__content{
        width: vw(900);
    }
    
} */

.home__item__wrapper{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    row-gap: vw(30);
    margin-top: vw(40);
    padding: 0 vw(70);

    .home__item{
        width: vw(214);
        height: vw(234);
        border: vw(1) solid #BABABA;
        cursor: pointer;

        .home__item__images{
            width: vw(214);
            height: vw(189);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        p.title{
            text-align: center;
            font-size: vw(15);
            font-family: 'Montserrat', sans-serif;
            font-weight: 450;
            padding-top: vw(15);
        }
    }
}

.btn__quiz--next{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: vw(47);
    right: vw(68);

    p{
        font-size: vw(27);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        text-transform: uppercase;
    }

    .btn__next__images{
        width: vw(27);
        height: vw(16);
        margin-left: vw(5);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

@media (min-width: 1600px){
    
    .quiz__nav{
        
        .quiz__nav__images{
            width: vhigh(498);
            height: vhigh(1005);
        }
    
        .quiz__pagination{
            top: vhigh(70);
            left: vhigh(78);
    
            .quiz__pag__item{
                font-size: vhigh(20);
                margin-top: vhigh(45);
            }
        }
    }
    
    .guiz__page{
     
        .quiz__page__content{
            height: vhigh(1005);
    
            p.header{
                padding-top: vhigh(70);
                font-size: vhigh(27);
            }
        }
    }
    
    .home__item__wrapper{
        row-gap: vhigh(30);
        margin-top: vhigh(40);
        padding: 0 vhigh(150);
    
        .home__item{
            width: vhigh(214);
            height: vhigh(234);
            border: vhigh(1) solid #BABABA;
    
            .home__item__images{
                width: vhigh(214);
                height: vhigh(189);
            }
    
            p.title{
                font-size: vhigh(15);
                padding-top: vhigh(15);
            }
        }
    }
    
    .btn__quiz--next{
        bottom: vhigh(47);
        right: vhigh(128);
    
        p{
            font-size: vhigh(27);
        }
    
        .btn__next__images{
            width: vhigh(27);
            height: vhigh(16);
            margin-left: vhigh(5);
        }
    }
}