.product__info{
    padding: vw(55) 0;

    p.header{
        text-align: center;
        text-transform: uppercase;
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        color: #222222;
        line-height: 110%;
        font-size: vw(37);
    }

    .product__info__container{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        column-gap: vw(95);
        row-gap: vw(50);
    }

    .product__info__item{
        margin-top: vw(40);
        cursor: pointer;
        width: vw(320);
        display: flex;
        flex-direction: column;
        align-items: center;

        .product__info__image{
            width: vw(150);
            height: vw(150);
            
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        p.product__info__text{
            font-size: vw(15);
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            margin-top: vw(15);
            text-align: center;
        }
    }
}

@media (min-width: 1600px){
    .product__info{
        padding: vhigh(55) 0;
    
        p.header{
            font-size: vhigh(37);
        }
    
        .product__info__container{
            column-gap: vhigh(95);
            row-gap: vhigh(50);
        }
    
        .product__info__item{
            margin-top: vhigh(40);
            width: vhigh(320);
    
            .product__info__image{
                width: vhigh(150);
                height: vhigh(150);
            }
    
            p.product__info__text{
                font-size: vhigh(15);
                margin-top: vhigh(15);
            }
        }
    }
}